









export default {
	data() {
		return {
			title:'',
			strings:''
		}
	},
	onLoad:function(e){
		uni.request({
			url: 'http://news-at.zhihu.com/api/2/news/'+e.id,
			method: 'GET',
			data: {},
			success: res => {
				this.title = res.data.title;
				this.strings = res.data.body;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}



